body {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.app {
    background-color: #d1e8e2;
    text-align: center;
}
.app-header {
    background-color: #116466;
    border-bottom: 5px solid #2c3531;
    left: 0;
    padding-top: 20px;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 190;
}
h1 {
    color: white;
    font-size: 26px;
}
.window {
    color: white;
    font-family: "Apple Chancery", "Ayuthaya", sans-serif;
    font-size: 14px;
    margin-right: 50px;
    text-align: right;
}
.app-header ul {
    font-size: 14px;
    list-style: none;
    margin: 0 auto;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}
.app-header ul li {
    color: white;
    display: inline-block;
    margin: 20px 7.5px;
}
a {
    text-decoration: none;
}
a:link,
a:visited {
    color: lightgray;
}
a:hover,
a:active {
    color: #ffcb9a;
    font-weight: 500;
}
a:focus {
    outline: 0;
}
@media only screen and (min-width: 450px){
    .app-header ul {
        font-size: 16px;
    }
    .app-header ul li {
        margin: 20px;
    }
}
.main-recipe-form {
    margin: 171px auto 0;
    max-width: 750px;
}
label {
    display: block;
    margin: 15px 0 0 15px;
    text-align: left;
}
input {
    border: 1px solid #ccc;
    box-shadow: 2px 2px 2px 0 #ccc;
    display: block;
    font-size: 16px;
    height: 30px;
    margin: 0 15px;
    text-align: left;
}
.ingredient {
    border: none;
    box-shadow: none;
}
.ingredients input {
    display: inline-block;
}
.qty,
.unit {
    width: 50px;
}
.instruction {
    border: none;
    box-shadow: none;
}

.instructions input {
    display: inline-block;
    width: 70%;
}
